export class HudPanel {
    constructor(root, store) {
        Object.defineProperty(this, "root", {
            enumerable: true,
            configurable: true,
            writable: true,
            value: void 0
        });
        Object.defineProperty(this, "store", {
            enumerable: true,
            configurable: true,
            writable: true,
            value: void 0
        });
        Object.defineProperty(this, "unsubscribe", {
            enumerable: true,
            configurable: true,
            writable: true,
            value: null
        });
        Object.defineProperty(this, "metricEls", {
            enumerable: true,
            configurable: true,
            writable: true,
            value: null
        });
        this.root = root;
        this.store = store;
    }
    mount() {
        this.root.classList.add("fd-hud");
        this.root.innerHTML = "";
        const metrics = {
            turn: this.createMetricBlock("Turn"),
            order: this.createMetricBlock("Order"),
            morale: this.createMetricBlock("Morale"),
            resources: this.createMetricBlock("Resources"),
            glitch: this.createMetricBlock("Glitch")
        };
        this.metricEls = metrics;
        for (const block of Object.values(metrics)) {
            this.root.appendChild(block);
        }
        this.unsubscribe = this.store.subscribe((state) => this.render(state));
    }
    destroy() {
        this.unsubscribe?.();
        this.metricEls = null;
        this.root.innerHTML = "";
    }
    render(state) {
        if (!this.metricEls) {
            return;
        }
        const { hud } = state;
        for (const key of Object.keys(this.metricEls)) {
            const element = this.metricEls[key];
            const label = element.querySelector(".fd-hud__metric-value");
            if (label) {
                label.textContent = String(hud[key] ?? 0);
            }
        }
    }
    createMetricBlock(label) {
        const block = document.createElement("div");
        block.className = "fd-hud__metric";
        const span = document.createElement("span");
        span.textContent = label;
        const value = document.createElement("div");
        value.className = "fd-hud__metric-value";
        value.textContent = "0";
        block.append(span, value);
        return block;
    }
}
