export class EventLogPanel {
    constructor(root, store) {
        Object.defineProperty(this, "root", {
            enumerable: true,
            configurable: true,
            writable: true,
            value: void 0
        });
        Object.defineProperty(this, "store", {
            enumerable: true,
            configurable: true,
            writable: true,
            value: void 0
        });
        Object.defineProperty(this, "listEl", {
            enumerable: true,
            configurable: true,
            writable: true,
            value: null
        });
        Object.defineProperty(this, "unsubscribe", {
            enumerable: true,
            configurable: true,
            writable: true,
            value: null
        });
        this.root = root;
        this.store = store;
    }
    mount() {
        this.root.classList.add("fd-panel");
        const title = document.createElement("div");
        title.className = "fd-panel__title";
        title.textContent = "Event Log";
        this.listEl = document.createElement("div");
        this.listEl.className = "fd-log";
        this.root.replaceChildren(title, this.listEl);
        this.unsubscribe = this.store.subscribe((state) => this.render(state));
    }
    destroy() {
        this.unsubscribe?.();
        this.listEl = null;
        this.root.innerHTML = "";
    }
    render(state) {
        if (!this.listEl) {
            return;
        }
        this.listEl.innerHTML = "";
        for (const entry of state.eventLog.slice(-8).reverse()) {
            const item = document.createElement("div");
            item.className = "fd-log__entry";
            item.textContent = `[T${entry.turn}] ${entry.text}`;
            this.listEl.appendChild(item);
        }
    }
}
