import { fetchStatus, fetchTurnTrace, fetchTurnTraces, runTurn, updateLlmMode } from "../api/client";
import { uiStore } from "../state/uiStore";
import { GridRenderer } from "../visuals/GridRenderer";
import { ControlsPanel } from "./panels/ControlsPanel";
import { EventLogPanel } from "./panels/EventLogPanel";
import { HudPanel } from "./panels/HudPanel";
import { DebugPanel } from "./panels/DebugPanel";
const GRID_SIZE = 12;
export class UIController {
    constructor(root, store = uiStore) {
        Object.defineProperty(this, "root", {
            enumerable: true,
            configurable: true,
            writable: true,
            value: void 0
        });
        Object.defineProperty(this, "store", {
            enumerable: true,
            configurable: true,
            writable: true,
            value: void 0
        });
        Object.defineProperty(this, "gridRenderer", {
            enumerable: true,
            configurable: true,
            writable: true,
            value: null
        });
        Object.defineProperty(this, "unsubscribeStore", {
            enumerable: true,
            configurable: true,
            writable: true,
            value: null
        });
        Object.defineProperty(this, "narrativeEl", {
            enumerable: true,
            configurable: true,
            writable: true,
            value: null
        });
        Object.defineProperty(this, "debugMode", {
            enumerable: true,
            configurable: true,
            writable: true,
            value: new URLSearchParams(window.location.search).get("debug") === "1"
        });
        Object.defineProperty(this, "debugPanel", {
            enumerable: true,
            configurable: true,
            writable: true,
            value: null
        });
        this.root = root;
        this.store = store;
    }
    async bootstrap() {
        const shell = document.createElement("div");
        shell.className = "fd-shell";
        const hudContainer = document.createElement("div");
        const hudPanel = new HudPanel(hudContainer, this.store);
        const layout = document.createElement("div");
        layout.className = "fd-layout";
        const stage = document.createElement("section");
        stage.className = "fd-stage";
        const stageTitle = document.createElement("div");
        stageTitle.className = "fd-stage__title";
        stageTitle.textContent = "Tactical Grid";
        const canvasHost = document.createElement("div");
        canvasHost.className = "fd-stage__canvas";
        this.narrativeEl = document.createElement("div");
        this.narrativeEl.className = "fd-stage__narrative";
        this.narrativeEl.textContent = "Awaiting first turn.";
        stage.append(stageTitle, canvasHost, this.narrativeEl);
        const panels = document.createElement("div");
        panels.className = "fd-panels";
        const logPanelNode = document.createElement("div");
        const logPanel = new EventLogPanel(logPanelNode, this.store);
        const controlsNode = document.createElement("div");
        const controlsPanel = new ControlsPanel(controlsNode, this.store);
        panels.append(logPanelNode, controlsNode);
        if (this.debugMode) {
            const debugNode = document.createElement("div");
            panels.appendChild(debugNode);
            this.store.enableDebug();
            this.debugPanel = new DebugPanel(debugNode, this.store);
            this.debugPanel.mount((turn) => {
                void this.loadTrace(turn);
            }, () => {
                void this.refreshTraceList();
            });
        }
        layout.append(stage, panels);
        shell.append(hudContainer, layout);
        this.root.replaceChildren(shell);
        hudPanel.mount();
        logPanel.mount();
        controlsPanel.mount((choiceId) => {
            void this.executeTurn(choiceId);
        }, (useStub) => {
            void this.setStubMode(useStub);
        });
        this.gridRenderer = new GridRenderer(canvasHost, { gridSize: GRID_SIZE, padding: 20 });
        await this.gridRenderer.init();
        this.unsubscribeStore = this.store.subscribe((state) => {
            this.gridRenderer?.setEntities(state.gridEntities);
            if (this.narrativeEl) {
                this.narrativeEl.textContent = state.narrative || "No narrative available.";
            }
        });
        this.store.setGridEntities(this.createDemoEntities());
        await this.refreshStatus();
        if (this.debugMode) {
            void this.refreshTraceList();
        }
    }
    destroy() {
        this.gridRenderer?.destroy();
        this.unsubscribeStore?.();
        this.debugPanel?.destroy();
    }
    createDemoEntities() {
        return [
            { id: "player", x: 5, y: 6, entityType: "player" },
            { id: "npc-1", x: 3, y: 2, entityType: "npc" },
            { id: "npc-2", x: 7, y: 8, entityType: "npc" },
            { id: "signal", x: 2, y: 9, entityType: "marker" }
        ];
    }
    async executeTurn(choiceId) {
        this.store.patch({ status: "running", error: undefined });
        try {
            const payload = await runTurn(choiceId);
            this.consumeTurnPayload(payload);
        }
        catch (error) {
            console.error("Failed to reach backend, falling back to simulation", error);
            await this.runLocalFallback();
        }
    }
    consumeTurnPayload(payload) {
        this.store.setGridEntities((payload.grid ?? []).map((cell) => this.mapGridCell(cell)));
        this.store.setHud(payload.hud);
        const logEntries = (payload.event_log ?? []).map((entry, idx) => ({
            id: entry.id ?? `${payload.hud.turn}-${idx}`,
            text: entry.text,
            turn: entry.turn ?? payload.hud.turn,
            timestamp: new Date().toISOString()
        }));
        this.store.setEventLog(logEntries);
        this.store.setOptions((payload.player_options ?? []).map((option, index) => this.normalizeOption(option, index)));
        this.store.patch({
            narrative: payload.narrative,
            atmosphere: payload.atmosphere,
            status: "idle",
            error: undefined
        });
        if (this.debugMode) {
            void this.refreshTraceList();
            if (payload.trace_file) {
                void this.loadTrace(payload.hud.turn);
            }
        }
    }
    mapGridCell(cell) {
        const normalizedType = cell.entityType === "npc_hostile" ? "npc" : cell.entityType;
        const entityType = normalizedType ?? "marker";
        const color = entityType === "player" ? 0x8b5cf6 : cell.hostile ? 0xf87171 : entityType === "npc" ? 0x38bdf8 : 0xfbbf24;
        return {
            id: cell.id,
            x: cell.x,
            y: cell.y,
            entityType: entityType,
            hostile: cell.hostile,
            label: cell.label,
            color
        };
    }
    normalizeOption(option, index) {
        const fromRecord = option;
        const idCandidate = (typeof fromRecord["id"] === "string" && fromRecord["id"]) ||
            (typeof fromRecord["choice_id"] === "string" && fromRecord["choice_id"]) ||
            `option_${index + 1}`;
        const labelCandidate = (typeof fromRecord["label"] === "string" && fromRecord["label"]) ||
            (typeof fromRecord["text"] === "string" && fromRecord["text"]) ||
            `Option ${index + 1}`;
        return {
            id: idCandidate,
            label: labelCandidate,
            description: typeof fromRecord["text"] === "string" ? fromRecord["text"] : undefined,
            type: typeof fromRecord["type"] === "string" ? fromRecord["type"] : undefined
        };
    }
    async runLocalFallback() {
        await new Promise((resolve) => setTimeout(resolve, 300));
        const state = this.store.getState();
        const nextTurn = state.hud.turn + 1;
        const rotated = state.gridEntities.map((entity, index) => ({
            ...entity,
            x: (entity.x + 1 + index) % GRID_SIZE,
            y: (entity.y + index) % GRID_SIZE
        }));
        this.store.setGridEntities(rotated);
        this.store.setHud({
            ...state.hud,
            turn: nextTurn,
            resources: Math.max(0, state.hud.resources - 1)
        });
        this.store.appendEvent("Offline simulation updated grid.", nextTurn);
        this.store.patch({
            status: "error",
            narrative: "Backend unreachable. Showing simulation data.",
            error: "Backend unreachable."
        });
    }
    async refreshTraceList() {
        if (!this.debugMode) {
            return;
        }
        try {
            const traces = await fetchTurnTraces();
            this.store.setDebugTraces(traces);
        }
        catch (error) {
            console.warn("Failed to refresh trace list", error);
        }
    }
    async refreshStatus() {
        try {
            const status = await fetchStatus();
            this.store.setLlmStatus({
                agents: status.llm ?? {},
                mode: status.mode === "stub" ? "stub" : "llm",
                version: status.version,
                useLlm: status.use_llm ?? status.mode !== "stub"
            });
        }
        catch (error) {
            console.warn("Failed to fetch LLM status", error);
        }
    }
    async setStubMode(useStub) {
        this.store.setSettings({ useStubAgents: useStub });
        try {
            const status = await updateLlmMode(!useStub);
            this.store.setLlmStatus({
                agents: status.llm ?? this.store.getState().llmStatus?.agents ?? {},
                mode: status.mode === "stub" ? "stub" : "llm",
                version: status.version,
                useLlm: status.use_llm ?? !useStub
            });
        }
        catch (error) {
            console.error("Failed to update LLM mode", error);
            this.store.setSettings({ useStubAgents: !useStub });
        }
    }
    async loadTrace(turn) {
        if (!this.debugMode) {
            return;
        }
        try {
            const payload = await fetchTurnTrace(turn);
            this.store.setActiveTrace(turn, payload);
        }
        catch (error) {
            console.warn("Failed to load trace", error);
        }
    }
}
